.error__subtitle {
    font-size: 2em;
}
.error__description {
    opacity: 0.5;
}
.error__button {
    min-width: 7em;
    margin-top: 3em;
    margin-right: 0.5em;
    padding: 0.5em 2em;
    outline: none;
    border: 2px solid #2f3640;
    background-color: transparent;
    border-radius: 8em;
    color: #576375;
    cursor: pointer;
    transition-duration: 0.2s;
    font-size: 0.75em;
    font-family: 'Righteous', cursive;
}
.error__button:hover {
    color: #21252c;
}
.error__button--active {
    background-color: #e67e22;
    border: 2px solid #e67e22;
    color: white;
}
.error__button--active:hover {
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.5);
